* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    width: 100%;
    max-width: 90%;
    margin: 0 auto;    
}

.container-documentos-page {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    padding: 10px;
    

}

.wrap-documentos {
    width: 390%;   
    border-radius: 10px;

    overflow: hidden;
    padding: 77px 55px 33px 55px;

    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
}

.container-documentos-form-btn {
    display: flex;
    flex-wrap: center;
    justify-content: center;
    padding-bottom: 30px;   
}

.documento-form-btn-atas,
.documento-form-btn-nf,
.documento-form-btn-convencao,
.documento-form-btn-documentos-importantes {

    font-size: 15px;
    border: none;
    border-radius: 10px;
    line-height: 1.2;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    background-color: #8A4E4E;
    color:white;
}


.documento-form-btn-voltar {
    font-size: 15px;
    border: none;
    border-radius: 10px;
    line-height: 1.2;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;       
    height: 50px;
    background-color: #8A4E4E;
    color:white;
    margin: 10px;
    margin-top: 20px;
    width: 15%;
    cursor: pointer;    
    
}

::placeholder {
    color: white;
    padding-left: 3px;
}

.documento-form-btn-atas:hover,
.documento-form-btn-nf:hover,
.documento-form-btn-convencao:hover,
.documento-form-btn-documentos-importantes:hover{
    cursor: pointer;
}

.documento-form-btn-upload {
    font-size: 15px;
    border: none;
    border-radius: 10px;
    line-height: 1.2;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 50px;
    background-color: #4C734C;
    color:white;
}

.inputfile {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.inputfile + label {    
    display: inline-block;
    font-size: 15px;
    border: none;
    border-radius: 10px;
    line-height: 1.2;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 50px;
    background-color: #8A4E4E;
    color:white;
    cursor: pointer;
    
}

.inputfile:focus + label,
.inputfile + label:hover {
    background-color: #593232;
}

.file-select {
    display: inline-block;
    font-size: 15px;
    border: none;
    border-radius: 10px;
    line-height: 1.2;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 50px;
    background-color: #808080;
    color:white;
    cursor: pointer;
}

.MuiSvgIcon-root {
    cursor: pointer;
}